$red: rgb(254, 82, 82);
$green: rgb(141, 217, 141);
$orange: rgb(250, 188, 111);
$blue: rgb(185, 200, 255);
$background: rgb(235, 235, 235);
$fire: rgb(254, 177, 114);
$yellow: rgb(250, 232, 115);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

html,
body {
  margin: 0;
}

.App {
  max-width: 100svw;
  max-height: 100svh;
  position: fixed;
  inset: 0;
}

.loading-page {
  justify-content: center;
  margin-top: 30svh;

  .pokeball {
    margin-top: 20px;
  }
}

.water-gradient {
  background: radial-gradient(circle closest-side, #3d5fe8, #e0ba6300);
}

.fire-gradient {
  background: radial-gradient(circle closest-side, #e45333, #e0ba6300);
}

.grass-gradient {
  background: radial-gradient(circle closest-side, #3ed33e, #e0ba6300);
}

.bug-gradient {
  background: radial-gradient(circle closest-side, #3ac83a, #e0ba6300);
}

.normal-gradient {
  background: radial-gradient(circle closest-side, #939393, #e0ba6300);
}

.poison-gradient {
  background: radial-gradient(circle closest-side, #a949bc, #e0ba6300);
}

.electric-gradient {
  background: radial-gradient(circle closest-side, #eeda2d, #e0ba6300);
}

.ground-gradient {
  background: radial-gradient(circle closest-side, #ecc97c, #e0ba6300);
}

.fairy-gradient {
  background: radial-gradient(circle closest-side, #ec5c9d, #e0ba6300);
}

.fighting-gradient {
