// Iphone ProMax 14
@media only screen and (min-height: 700px) {
  .nav {
    .nav-items {
      font-size: 32px;
      span {
        vertical-align: top;
        font-size: 28px;
        padding-left: 10px;
      }
    }
  }

  .page {
    .searchbar {
      .enter {
        bottom: 9px;
        font-size: 24px;
      }

      .icon {
        bottom: 6px;
        font-size: 28px;
      }
      input {
        line-height: 55px;
        font-size: 22px;
        padding-left: 60px;
      }
    }

    .homepage-inner {
      padding-top: 180px;

      .home-image {
        width: 80svw;
        top: 100px;
      }

      h1 {
        font-size: 35px;
      }

      .lower {
        padding-top: 50px;

        .button-container {
          gap: 25px;

          .button {
            padding: 17px 30px;
            font-size: 14px;

            .button-icon {
              font-size: 26px;
            }
          }
        }
      }
    }

    .search-inner {
      .mini-button-container {
        gap: 7px;
        margin-top: 30px;

        .mini-button {
          height: 65px;
          font-size: 14px;
          margin-bottom: 10px;
        }
        .unknown {
          display: none;
        }

        .shadow {
          display: none;
        }
      }
