              li:last-child {
                border-radius: 0px 25px 25px 0px;
              }
            }
  
            .display-container {
              width: 35svw;
              margin-top: 2svh;
              min-height: fit-content;
              overflow: hidden;

  
              .wrapper {
                gap: 7px;
                min-height: 30svh;
                max-height: 30svh;
                div {
                  display: flex;
  
                  .stat-name {
                    width: 10%;
                    font-size: 22px;
                  }
  
                  .progress-outter {
  
                    span {
                      position: absolute;
                      right: 10px;
                      font-size: 20px;
                    }
  
                    .progress-bar {
                      min-height: 28px;
                    }
                  }
                }
              }
  
              // .wrapper::-webkit-scrollbar {
              //   display: none;
              // }
  
              .about-pokemon {
                .pokemon-about-me-desc {
                  font-size: 20px;
                }
  
                .height-weight {
                  gap: 15px;
  
                  .height,
                  .weight,
                  .strong {
                    font-size: 20px;
                  }
  
                  .strong {
                    .strength-icon {
                      max-height: 35px;
                      max-width: 35px;
                    }
  
                    .strength-icon:first-of-type {
                      margin-left: 20px;
                    }
                  }
                }
              }
  
              .moves {
                display: flex;
                width: 35svw;
                flex-direction: row;
                gap: 0;
                justify-content: space-around;
                max-height: fit-content;
  
                .attack-card {
                  display: flex;
                  flex-direction: column;
                  max-width: 100%;
                  min-width: 17svw;
                  padding: 10px;
                  text-align: center;
                  height: 100%;
  
                  h3 {
                    font-size: 22px;
                    color: grey;
                    margin-left: -35px;
                  }
                  // background-color: yellow;
  
                  .attack-types,
                  .attack-stats {
                    display: flex;
                    flex-direction: column;
                    width: 90%;
  
                    .stat-headers,
                    .stat-values {
                      display: flex;
                      justify-content: space-between;
                      font-size: 20px;
                      margin-bottom: 5px;
                    }
  
                    .stat-headers {
                      font-size: 20px;
                      color: grey;
                    }
  
                    .attack-type {
                      text-align: center;
                      width: 80px;
                      padding: 5px;
                      border: solid 1px white;
                      margin: auto;
                      display: flex;
                      justify-content: space-around;
                      margin-top: 45px;
                      margin-bottom: 45px;
                      border-radius: 25px;
                      font-size: 16px;
                    }
                  }
                }
  
                .right {
                  .attack-card {
                    border-left: grey solid 2px;
                    padding-left: 22px;
                    padding-right: 0;
                  }
                }
              }
            }
          }
        }
  
        .footer {
          width: 50svw;
          min-width: 50svw;
          margin: auto;
          min-height: 60px;
          height: calc(100svh + 60px);
          background-color: white;
          z-index: 10000000;
          position: fixed;
          top: -60px;
          left: auto;
          right: 0;
          border-radius: 25px 25px 0px 0px;
          box-shadow: rgba(0, 0, 0, 0.237) 0 0 20px;
          border-collapse: collapse;
  
          .button-container {
            // background-color: pink;
            height: 60px;
            width: 100%;
            padding: 20px;
  
            .footer-btn {
              width: 25%;
              background: none;
              border: none;
              font-size: 20px;
              margin: auto;
  
              img {
                width: 35px;
              }
            }
  
            .red {
              color: red;
            }
          }
  
          .small {
            top: 0px;
          }
  
          .small-inner {
            height: 12svh;
            margin: auto;
            min-height: 12svh;
            background-color: white !important;
  
            .back-nav {
              padding: 30px;
              font-size: 30px;
              color: black;
  
              span {
                font-size: 27px;
                vertical-align: top;
                display: none;
              }
            }
          }
  
          .evolution-chain-container,
          .location-info {
            z-index: 50000000;
            color: black;
            margin: auto;
            padding-top: 0px;
            position: relative;
            font-size: 30px;
            width: 50svw;
            position: absolute;
            left: auto;
            height: 100%;
            min-height: 90svh;
            border-radius: 0;
            justify-content: center;
            text-align: center;
            background-color: white;
  
            h1 {
              font-size: 30px;
            }
  
            .evo-pokemon {
              h2 {
                font-size: 18px;
              }
  
              img {
                height: 150px;
              }
  
              .type {
                margin: auto;
                width: 60px;
                text-align: center;
                font-size: 14px;
                border: 1px solid rgb(188, 187, 187);
                border-radius: 25px;
                padding: 3px;
              }
  
              span {
                font-size: 20px;
              }
            }
          }
          .location-info {
            overflow-y: scroll;
            padding-bottom: 150px;
  
            .generation-footer {
              background-color: grey;
              min-height: 2px;
              width: 80svw;
              margin: auto;
              margin-top: 40px;
            }
  
            .nope {
              margin-top: 30px;
              font-size: 20px;
            }
  
            .generation {
              text-align: left;
              max-width: 100svw;
              margin: auto;
  
              h3 {
                padding-left: 30px;
                font-size: 18px;
                margin-top: 40px;
              }
  
              .location-container {
                max-width: 40svw;
                margin: auto;
                padding: 10px 30px;
  
                  .location-name {
                    font-size: 16px;
                  }
              }
            }
          }
  
          .hide {
            display: none;
          }
        }
  
        .small {
          top: calc(100svh - 60px);
        }
      }
    }
  }


}

