@media only screen and (min-height: 1000px) and (max-height: 1100px) and (max-width: 1100px) {
.gradient {
  top: -5svh !important;
  margin-left: -2svw !important;
}
}

// Ipad air
@media only screen and (min-width: 800px) and (max-width: 850px) and (min-height: 1100px) and (max-height: 1200px) {
  .gradient {
    top: 0svh !important;
  }

  .img-container {
    img {
      top: 40px !important;
    }
  }

  .pokemon-type {
    bottom: -230px !important;
  }

  .navigate-buttons {
    margin-top: -200px !important;
  }

  .tab-content {
    margin-top: 100px !important;
  }
  }

  // Ipad Pro
  @media only screen and (min-width: 1000px) and (max-width: 1100px) and (min-height: 1300px) and (max-height: 1400px) {
    .gradient {
      top: 6svh !important;
      left: -20svw !important;
    }
  
    .img-container {
      img {
        top: 40px !important;
        max-width: 65svw !important;
        min-width: 65svw !important;
      }
    }

    .pokemon-type {
      bottom: -270px !important;
    }
  
    .navigate-buttons {
      margin-top: -200px !important;
    }
  
    .tab-content {
      margin-top: 100px !important;
    }
  }






